"use client";

import { useState } from 'react';
import styles from './ContentForm.module.css';

export default function ContentForm({ onGenerate }: { onGenerate: (topic: string) => void }) {
    const [topic, setTopic] = useState('');
    const [isLoading, setIsLoading] = useState(false);

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        if (!topic.trim()) return;

        setIsLoading(true);
        await onGenerate(topic);
        setIsLoading(false);
    };

    return (
        <form className={styles.form} onSubmit={handleSubmit}>
            <div className={styles.inputGroup}>
                <label htmlFor="topic" className={styles.label}>What do you want to post about?</label>
                <textarea
                    id="topic"
                    className={styles.textarea}
                    placeholder="e.g., The importance of work-life balance in 2024..."
                    value={topic}
                    onChange={(e) => setTopic(e.target.value)}
                    rows={4}
                />
            </div>
            <button type="submit" className={styles.button} disabled={isLoading || !topic.trim()}>
                {isLoading ? 'Generating...' : 'Generate Post'}
            </button>
        </form>
    );
}
